<template>
  <div class="carVideo">
    <div>
      <div class="bkgray"></div>
      <div class="video-row" v-for="(items,i) of this.result" :key="i">
        <div class="video-row-head">
          <div>{{items.CatName}}</div>
        </div>
        <van-row gutter="10">
          <van-col
            span="12"
            v-for="item of items.CatList"
            :key="item.VideoId"
            style="margin-bottom: 10px;"
            @click="Clickfn(item)"
          >
            <van-image width="100%" height="100%" :src="item.ImageLink">
              <template v-slot:loading>
                <van-loading type="spinner" size="20" />
              </template>
            </van-image>
            <p style="font-size: 12px;">{{item.Title}}</p>
            <p style="color: #999999; font-size: 10px;">
              <span>播放数:{{item.TotalVisit}}</span>
              <span style="float: right;">时长:{{item.Duration}}</span>
            </p>
          </van-col>
        </van-row>
      </div>
      <div class="bkgray"></div>
    </div>
    <div class="ding"></div>
  </div>
</template>

<script>
import * as api from "@/api/index.js";
export default {
  data() {
    return {
      result: ""
    };
  },
  created() {
    this.getVideo();
  },
  methods: {
    //获取接口数据
    async getVideo() {
      let res = await api.getVideo();
      // console.log(res)
      this.result = res.Data;
    },
    Clickfn(item) {
      //将获取到的接口输入传入视频详情页中
      this.$router.push({ path: "/videoDetail", query: { item: item } });
      // console.log(item)
    }
  }
};
</script>

<style scoped="scoped">
.ding {
  width: 100%;
  height: 50px;
}
.carVideo .bkgray {
  background-color: #f5f5f5;
  width: 100%;
  height: 10px;
}

.carVideo .video-row {
  padding: 24px 30px 0;
}

.video-row-head {
  padding-bottom: 12px;
}

.video-list {
  display: flex;
  flex-wrap: wrap;
}
</style>
