<template>
  <div class="newCar">
    <van-list v-model="loading" :finished="finished" finished-text="没有更多了" @load="onLoad">
      <div class="item-row" v-for="(item,i) of list" :key="i">
        <router-link :to="{path:'/detail',query:{newsid:item.newsid}}">
          <div class="left">
            <van-image width="120" height="80" :src="item.PicCover">
              <template v-slot:loading>
                <van-loading type="spinner" size="20" />
              </template>
            </van-image>
          </div>
          <div class="right">
            <div class="right-top">{{item.title}}</div>
            <div class="right-bottom">
              <span>{{item.publishtime}}</span>
              <span>评论：{{item.CommentCount}}</span>
            </div>
          </div>
        </router-link>
      </div>
    </van-list>
    <div class="ding"></div>
  </div>
</template>

<script>
import axios from "axios";
export default {
  data() {
    return {
      list: [],
      num: 1,
      loading: false,
      finished: false
    };
  },
  methods: {
    onLoad() {
      // 异步更新数据
      let _self = this;
      setTimeout(() => {
        axios
          .get("https://wxs.app.yiche.com/newslist.ashx", {
            params: {
              cateid: 2,
              pageindex: _self.num,
              pagesize: 25,
              t: 0
            }
          })
          .then(function(res) {
            // window.console.log(res);
            _self.list.push(...res.data.Data);
            _self.num++;
          });
        // 加载状态结束
        this.loading = false;

        // 数据全部加载完成
        if (this.list.length >= 200) {
          this.finished = true;
        }
      }, 500);
    }
  }
};
</script>

<style>
.newCar .ding {
  width: 100%;
  height: 50px;
}

.newCar .item-row .right .right-bottom {
  color: #999;
  display: flex;
  justify-content: space-between;
  font-size: 12px;
}

.newCar .item-row .right .right-top {
  word-wrap: break-word;
  flex: 1;
  font-size: 15px;
  color: #333;
}

.newCar .item-row .right {
  flex-direction: column;
  padding-left: 20px;
  flex: 1;
  display: flex;
  align-content: space-between;
}

.newCar .item-row {
  padding: 15px 15px 0;
  display: flex;
  border-bottom: 1px solid #f0f0f0;
  justify-content: space-between;
}
</style>
