<template>
  <div class="news">
    <!-- 轮播 -->
    <swiper style="position: relative;">
      <swiper-slide v-for="item of swipeList" :key="item.newsid">
        <img :src="item.FocusImg" alt width="100%" height="100%" />
        <div class="title">
          <div class="title-item">{{item.title}}</div>
        </div>
      </swiper-slide>
    </swiper>
    <!-- 头条新闻 -->
 <van-list v-model="loading" :finished="finished" finished-text="没有更多了" @load="onLoad">
    <div class="item-row" v-for="(item,i) of list" :key="i">
      <router-link :to="{path:'/detail',query:{newsid:item.newsid}}">
        <div class="left">
          <van-image width="120" height="80" :src="item.PicCover">
            <template v-slot:loading>
              <van-loading type="spinner" size="20" />
            </template>
          </van-image>
        </div>
        <div class="right">
          <div class="right-top">{{item.title}}</div>
          <div class="right-bottom">
            <span>{{item.author}}</span>
            <span>评论：{{item.CommentCount}}</span>
          </div>
        </div>
      </router-link>
    </div>
	</van-list>

    <!-- 把下面撑上来 -->
    <div class="ding"></div>
  </div>
</template>

<script>
import * as api from "@/api/index.js";
import axios from "axios";
export default {
  created() {
    this.getnews();
  },
  data() {
    return {
      current: 0,
      list: [],
      num: 3,
      loading: false,
      finished: false,
      swipeList: ""
    };
  },
  methods: {
    onChange(index) {
      this.current = index;
    },
    async getnews() {
      let res = await api.getnews();
      this.swipeList = res.FocusData;
    },
    onLoad() {
      // 异步更新数据
      let _self = this;
      setTimeout(() => {
        axios
          .get("https://wxs.app.yiche.com/newslist.ashx", {
            params: {
              cateid: 10000,
              pageindex: _self.num,
              pagesize: 25,
              t: 0
            }
          })
          .then(function(res) {
            // window.console.log(res);
            _self.list.push(...res.data.Data);
            _self.num++;
          });
        // 加载状态结束
        this.loading = false;

        // 数据全部加载完成
        if (this.list.length >= 200) {
          this.finished = true;
        }
      }, 500);
    }
  }
};
</script>

<style>
.left {
  float: left;
}

.news .ding {
  width: 100%;
  height: 50px;
}

.news .item-row .right .right-bottom {
  color: #999;
  display: flex;
  justify-content: space-between;
  font-size: 12px;
}

.news .item-row .right .right-top {
  word-wrap: break-word;
  flex: 1;
  font-size: 15px;
  color: #333;
}

.news .item-row .right {
  height:80px;
  flex-direction: column;
  padding-left: 20px;
  flex: 1;
  display: flex;
  align-content: space-between;
}

.news .item-row {
  padding: 15px 15px 0;
  display: flex;
  border-bottom: 1px solid #f0f0f0;
  justify-content: space-between;
}

.news .title {
  width: 98%;
  padding-left: 5px;
  padding-right: 5px;
  position: absolute;
  bottom: 5px;
  font-size: 13px;
  color: white;
  background-image: linear-gradient(
    -180deg,
    rgba(0, 0, 0, 0) 0,
    rgba(0, 0, 0, 0.85) 100%
  );
}

.news .title .title-item {
  width: 80%;
  overflow: hidden;
  white-space: nowrap;
  text-overflow: ellipsis;
  color: #fff;
  font-size: 28rpx;

  float: left;
}

.news .indicator {
  float: right;
}

.news .swipeVan {
  position: relative;
}
</style>
